.aboutContainer {
    background-repeat: repeat;
    max-height: fit-content;
    color:white;
    font-family:'Roboto', sans-serif;
    display:flex;
    flex-direction: column;
    justify-content: center;
    flex-wrap: wrap;
    width: 100%;
    height:100%;
    overflow-x: scroll;
    flex-grow: 1;
}

.heading
{
text-align: center;
display: flex;
flex-direction: column;
justify-content: center;
align-items: center;
align-content: center;
font-size: 20px;
}
.title
{
    flex:1;
    margin: 0;
    padding: 8px;
}
* {
    box-sizing: border-box;
  }
  
  .container {
    padding: 64px;
  }
  
  /* Clear floats */
  .row:after {
    content: "";
    display: table;
    clear: both
  }
  
  /* 2/3 column */
  .column-66 {
    float: left;
    width: 50%;
    padding: 20px;
  }
  
  /* 1/3 column */
  .column-33 {
    float: left;
    width: 50%;
    padding: 20px;
  }
  
  /* Add responsiveness - make the columns appear on top of each other instead of next to each other on small screens */
  @media screen and (max-width: 1000px) {
    .column-66,
    .column-33 {
      width: 100%;
      text-align: center;
    }
  }

