.const {
    display: grid;
    height: 100%;
    background-size: cover;
  
  }
  
  .header{
    width: 100%;
    height: 100vh;
    position: relative;
    perspective: 100rem;  
  }
  
  .img{
    width: 100%;
    height: 100%;
  }
  
  .welcomeNote {
    font-family: 'Times New Roman', Times, serif;
    color: #fff;
    text-shadow: 0 0.3rem 0.5rem rgba(0, 0, 0, 0.4);
    opacity: 1;
    animation: moveBanner 1.5s 0s forwards;
  }

  @media screen and (max-width: 1000px) {
    .welcomeNote {
    font-size: 4rem;
    font-weight: 250;
    width: 50%;
    line-height: 5rem;
    }
  }

  @media screen and (max-width: 600px) {
    .welcomeNote {
    font-size: 4rem;
    font-weight: 250;
    width: 100%;
    line-height: 5rem;
    }
  }

  @media screen and (min-width: 1000px) {
    .welcomeNote {
    font-size: 5rem;
    font-weight: 250;
    width: 50%;
    line-height: 5rem;
    }
  }
  
  @media screen and (max-width: 600px) {
    .wcIcon {
      display: none;
    }
  }

  @media screen and (min-width: 600px) {
    .wcIcon {
      display:inline-block;
      margin-right: 2%;
    }
  }

  @media screen and (max-width: 600px) {
    .topIcon {
      display: inline-block;
      margin-right: 2%;
    }
  }

  @media screen and (min-width: 600px) {
    .topIcon {
      display: flex;
      justify-content: center;
    }
  }
  
  .img img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    opacity: 1;
    animation: scale 25s;
  }
  
  .nitl{
    width: 10%;
    height: 10%;
    padding-left: 60%;
    
    position: relative;
    padding-bottom: 20%;
    border-radius: 50%;
    
  }
  
  
  
  
  .banner{
    position:absolute;
    top: 20%;
    left: 10%;
  }
  
  
  .banner p {
        
    font-family: "Josefin Slab", serif;
    font-size: 1.5rem;
    color: #fff;
    width: 60%;
    letter-spacing: 0.1rem;
    margin-bottom: 4rem;
    text-shadow: 0 0.3rem 0.5rem rgba(0, 0, 0, 0.4);
    opacity: 1;
    animation: moveBanner 1s 0s forwards;
  }
  

  @keyframes moveBanner {
    0% {
      transform: translateY(40rem) rotateY(-20deg);
    }
    100% {
      transform: translateY(0) rotateY(0);
      opacity: 1;
    }
  }
  
  
  /* Style page content - use this if you want to push the page content to the right when you open the side navigation */
  #main {
    transition: margin-left .5s;
    padding: 0px;
  }
  
  /* On smaller screens, where height is less than 450px, change the style of the sidenav (less padding and a smaller font size) */
   @media screen and (max-height: 450px) {
    .sidenav {padding-top: 15px;}
    .sidenav Link, button {font-size: 18px;} 
   }
  
   .icon {
    position: fixed;
    top: 50%;
    -webkit-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    transform: translateY(-50%);
  }
  .icon a {
    display: block;
    text-align: center;
    padding: 16px;
    transition: all 0.3s ease;
    color: white;
    font-size: 20px;
  }
  
  /* Style the social media icons with color, if you want */
  .icon a:hover {
    background-color: #000;
  }
  
  .facebook {
    background: #3B5998;
    color: white;
  }
  .insta {
    background: #55ACEE;
    color: white;
  }
  
  /* css for modal  */
  .bg-modal
  {
    width:100%;
    height:100%;
    background-color: rgba(0, 0, 0, 0.7);
    position: absolute;
    top:0;
    display: flex;
    justify-content: center;
    align-items: center;
    display: none;
  }
  .modal-content{
    width:550px;
    height:400px;
    background-color: white;
    border-radius: 4px;
    text-align: center;
    position: relative;
  }
  